import React from 'react';
import { default as MuiTextareaAutosize, TextareaAutosizeProps } from '@mui/material/TextareaAutosize';

const TextareaAutosize = React.forwardRef<HTMLElement, TextareaAutosizeProps>( ( props, ref ) => {
	return (
		<MuiTextareaAutosize
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TextareaAutosizeProps>;

export default TextareaAutosize;
